import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";
import MultiCartItemIconCellComponent from "./MultiCartItemIconCellComponent";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent"
import DataLayerUtils from "eshop/utils/DataLayerUtils";
import ProductOfferPriceTooltipComponent from "eshop/modules/simfree/components/ProductOfferPriceTooltipComponent"
import {createSimCardDescription} from "./../utils/CartUtils";
import OnlineUtils from "eshop/utils/OnlineUtils";
import VASPackagesPresentationComponent from "./VASPackagesPresentationComponent";


const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
}

export default class MultiCartItemHeaderComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    getMsisdnForProcess() {
        if(this.props.processType === "ACTIVATION") {
            return this.props.msisdn ? DataLayerUtils.processTypeToMsisdnDescriptionMap["ACTIVATION"] + this.transformMsisdn(this.props.msisdn) : '';
        } else if( OnlineUtils.isAssignment(this.props.processType) ) {
            return this.props.msisdn ? DataLayerUtils.processTypeToMsisdnDescriptionMap[this.props.processType] + this.transformMsisdn(this.props.msisdn) : '';
        } else {
            return this.props.msisdnVerificationData ? DataLayerUtils.processTypeToMsisdnDescriptionMap[this.props.processType] +  this.transformMsisdn(this.props.msisdnVerificationData.msisdn) : '';
        }
    }
    getMNPApplication(){
        return DataLayerUtils.processTypeToMsisdnDescriptionMap["MNP_APPLICATION"] + '\xa0' + this.transformMsisdn(this.props.msisdnVerificationData.msisdn);
    }
    renderPrice(className = null) {
        return this.props.monthlyPricePresentation.main ? this.renderPriceDesktop(this.props.monthlyPricePresentation.main, this.props.stockLevelStatus, className ) : this.renderVoidDesktop();
    }
    renderOneTimePrice () {
        return <div>{this.renderPriceDesktop(this.props.oneTimePrice, this.props.stockLevelStatus)}</div>;
    }
    showChangeSimButton() {
        return this.props.onMsisdnChangeClicked;
    }
    showLowerInstallments(){
        return this.props.onChangeClicked && this.props.lowerInstallmentsClicked && !!this.props.monthlyPricePresentation && !!this.props.monthlyPricePresentation.main && (this.props.monthlyPricePresentation.main.price != 0 || this.props.monthlyPricePresentation.main.priceWithoutVouchers != 0);
    }
    getClassForPrice(){
        return this.showLowerInstallments() ?  "l-col-2 u-padding-xs u-padding-top-l u-padding-left u-text-right" :  "l-col-2 u-padding-l u-padding-top-l u-padding-left u-text-right";
    }


    render() {
        console.warn("#***************************MultiCartItemHeaderComponent**********************************");
        console.warn(this.props.monthlyPricePresentation);
        console.warn("***************************MultiCartItemHeaderComponent**********************************#");

        return (
            <tr>
              <MultiCartItemIconCellComponent entryType={this.props.type}
                                              image={this.props.imageUrl}
                                              icon={this.props.icon}
                                              rowSpan={this.props.type==='VAS' ? undefined : "4"}
                                              terminalName={this.props.title}/>
              <td colSpan={this.props.priceless && '3'} className="u-padding-l u-padding-top-l u-padding-right-l">
                <div className="u-left">{this.props.processName&&
                    <p className={"u-font-bold g-gray5-c"}>{this.props.processName}</p>}
                    <span className="u-font-bold">{ this.props.title }</span><br/>
                    {this.props.loyaltyLengthDescription && <p> {this.props.loyaltyLengthDescription} </p>}
                    {this.props.instalmentLengthDescription && <p> {this.props.instalmentLengthDescription} </p>}
                    {this.props.editable && this.props.processType
                     && [ this.props.simCard && createSimCardDescription(this.props.simCard)
                        , this.getMsisdnForProcess()
                        , this.showChangeSimButton.apply(this)
                        && <a className="u-spacing-left" href="#" key='msisdnChangeClicked_key'
                              onClick={ preventDefaultWrapper(this.props.onMsisdnChangeClicked)}
                              aria-label="zmień numer telefonu lub rodzaj karty sim">
                                Zmień
                           </a>
                        ]
                    }
                    <VASPackagesPresentationComponent editable={this.props.editable} onVasPackagesChangeClicked={this.props.changePackages}
                                                      vases={this.props.vases} bundleCode={this.props.bundleCode}/>
                    {( OnlineUtils.isAssignment(this.props.processType) )
                        && this.getMsisdnForProcess()
                    }
                    {(OnlineUtils.isMnpApplication(this.props.processType))
                        && this.getMNPApplication()
                    }
                    {this.props.editable&&
                    <div className="u-spacing-top">
                      {this.props.onChangeClicked &&<a href="#" className="u-spacing-right-l" onClick={ preventDefaultWrapper(this.props.onChangeClicked) } aria-label={"zmień "+this.props.title}>Zmień</a>}
                      {this.props.onDetailsClicked &&<a href="#" className="u-spacing-right-l" onClick={ preventDefaultWrapper(this.props.onDetailsClicked) } aria-label={"wyświetl szczegółowy opis dla "+this.props.title}>Szczegóły</a>}
                      {this.props.onRemoveClicked &&<a href="#" onClick={ preventDefaultWrapper(this.props.onRemoveClicked) } aria-label={"usuń " + this.props.title + " z koszyka"}>Usuń</a>}
                    </div>
                    }
                 </div>
                 <div className="u-right" style={{width: '50%'}}>
                    <OraStockLevelStatusComponent stockLevelStatus={this.props.stockLevelStatus} shouldStockLevelBeVisible={this.props.shouldStockLevelBeVisible}/>
                 </div>
              </td>
                { !this.props.priceless &&
                <td className="l-col-2 u-padding-l u-padding-top-l u-padding-right u-text-right">
                    <span className="u-acc-hide">Opłaty jednorazowe</span>
                    {this.props.oneTimePrice ? this.renderOneTimePrice() : this.renderVoidDesktop()}
                    {this.props.oneTimePrice.crossedOut &&
                    <del className="h4 u-font-small g-gray5-c u-inline-block">
                        {this.props.oneTimePrice.crossedOut}
                    </del>
                    }
                </td>
              }
              { !this.props.priceless &&
                <td className={this.getClassForPrice()}>
                  <span className="u-acc-hide">Opłaty miesięczne</span>

                    {this.props.monthlyPricePresentation.tooltip &&  !OnlineUtils.isAssignment(this.props.processType) ?
                        <ProductOfferPriceTooltipComponent key={this.props.monthlyPricePresentation.tooltip.id} {...this.props.monthlyPricePresentation.tooltip}>
                            {this.renderPrice()}
                        </ProductOfferPriceTooltipComponent>
                        :
                        this.renderPrice()
                    }

                  { this.props.monthlyPricePresentation.crossedOut &&
                      <del className="h4 u-font-small g-gray5-c u-inline-block">
                        { this.props.monthlyPricePresentation.crossedOut }
                      </del>
                  }

                  { this.props.monthlyPricePresentation.subtitle &&
                      <div className="g-gray5-c u-font-small u-clear-both">
                        { this.props.monthlyPricePresentation.subtitle }
                      </div>
                  }

                    {this.showLowerInstallments() &&
                    <div>
                        <a className="u-spacing-left" href="#" key='lowerInstallmentsClicked_key'
                           onClick={preventDefaultWrapper(this.props.lowerInstallmentsClicked)}
                           aria-label="Obniż raty">{!!this.props.descriptions && this.props.descriptions.lowerInstallments || "Obniż raty"}
                        </a>
                    </div>
                    }
                </td>
              }
            </tr>
        )
    }
}